import {
  actionSelectCharacter,
  actionSetActiveTabIndex,
  actionUpdateCharacter,
  SelectCharacterAction,
  SetActiveTabIndexAction,
  UpdateCharacterAction,
  Character
} from '../store';

export const selectCharacter = (id: string): SelectCharacterAction => ({
  type: actionSelectCharacter,
  id
});

export const setActiveTabIndex = (index: number): SetActiveTabIndexAction => ({
  type: actionSetActiveTabIndex,
  index
});

export const updateCharacter = (character: Character): UpdateCharacterAction => ({
  type: actionUpdateCharacter,
  character
});
