import * as React from 'react';

interface TabPanelProps {
  index: number;
  value: number;
}

const TabPanel = (props: React.PropsWithChildren<TabPanelProps>) => {
  const { index, value, children } = props;
  return (
    <React.Fragment>
      {
        index === value &&
        <React.Fragment>
          {children}
        </React.Fragment>
      }
    </React.Fragment>
  );
};

export default TabPanel;
