import * as React from 'react';
import {
  Theme,
  List,
  ListItem,
  ListItemText,
  Divider,
  Tooltip
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import {
  NormalizedList,
  Learnable,
  Character,
  SkillCalculationRequirements,
  JobClass
} from '../store';

const useStyles = makeStyles((theme: Theme) => ({
  tooltip: {
    fontSize: '1rem'
  }
}));

interface EquippableAbilityListProps {
  characterLearnables: Learnable[];
  availableAbilityLearnables: Learnable[];
  equipAbility: (abilityId: string, character: Character | undefined, characterLearnables: Learnable[], requirements: SkillCalculationRequirements) => void;
  character: Character;
  disabled?: boolean;
  skillLevels: string[];
  classId: string;
  classes: NormalizedList<JobClass>;
  equippedLearnables: Learnable[];
}

const EquippableAbilityList = (props: React.PropsWithChildren<EquippableAbilityListProps>) => {
  const {
    availableAbilityLearnables,
    equipAbility,
    character,
    disabled,
    skillLevels,
    classId,
    classes,
    equippedLearnables,
    characterLearnables
  } = props;
  const styleClasses = useStyles();
  return (
    <List>
      <ListItem>
        <ListItemText secondary="Available Abilities" />
      </ListItem>
      {availableAbilityLearnables.map((learnable) => (
        <React.Fragment key={learnable.id}>
          <Tooltip title={learnable.description} classes={styleClasses}>
            <span>
              <ListItem
                disabled={disabled}
                button
                onClick={() => equipAbility(learnable.id, character, characterLearnables, {
                  skillLevels,
                  classId,
                  classes,
                  equippedLearnables
                })}
              >
                <ListItemText
                  primary={learnable.id}
                />
              </ListItem>
            </span>
          </Tooltip>
          <Divider component="li" />
        </React.Fragment>
      ))}
    </List>
  );
};

export default EquippableAbilityList;
