import * as React from 'react';
import {
  List,
  ListItem,
  ListItemText,
  Divider
} from '@material-ui/core';
import {
  JobClass,
  NormalizedList,
  Character,
  SkillCalculationRequirements,
  Learnable
} from '../store';
import { filterNormalizedList } from '../utils';

interface ClassListProps {
  classes: NormalizedList<JobClass>;
  selectClass: (character: Character | undefined, characterLearnables: Learnable[], requirements: SkillCalculationRequirements) => void
  character: Character;
  skillLevels: string[];
  equippedLearnables: Learnable[];
  characterLearnables: Learnable[]
}

const ClassList = (props: React.PropsWithRef<ClassListProps>) => {
  const {
    selectClass,
    classes,
    character,
    skillLevels,
    equippedLearnables,
    characterLearnables
  } = props;
  const availableClasses = filterNormalizedList(classes, character.availableClasses);
  return (
    <List>
      {availableClasses.allIds.map((classId) => (
        <React.Fragment key={classId}>
          <ListItem
            button
            selected={classId === character.selectedClassId}
            onClick={() => selectClass(character, characterLearnables, {
              classes,
              skillLevels,
              classId,
              equippedLearnables
            })}
          >
            <ListItemText primary={classes.byId[classId].id} />
          </ListItem>
          <Divider />
        </React.Fragment>
      ))}
    </List>
  );
};

export default ClassList;
