import * as React from 'react';
import { connect } from 'react-redux';
import {
  ApplicationState,
  CharacterPlannerState,
  Character,
  JobClass,
  Ability,
  Learnable,
  SkillCalculationRequirements
} from '../store';
import { calculateSkillLevels, calculateClassMasteriesRequired } from '../utils';
import * as CharacterPlannerStore from '../store/characterPlanner';
import { bindActionCreators } from 'redux';
import {
  Container,
  Grid,
  Typography,
  Tabs,
  Tab,
  AppBar
} from '@material-ui/core';
import { default as TabPanel } from './tabPanel';
import { default as AbilityList } from './abilityList';
import { default as CharacterList } from './characterList';
import { default as ClassList } from './classList';
import { default as EquippableAbilityList } from './equippableAbilityList';
import { default as SkillList } from './skillList';
import { default as MagicList } from './magicList';
import { default as StatusClassList } from './statusClassList';
import { default as EquippableCombatArtsList } from './equippableCombatArtsList';
import { default as CombatArtsList } from './combatArtsList';

type CharacterPlannerProps =
  CharacterPlannerState &
  typeof CharacterPlannerStore.actionCreators;

class CharacterPlanner extends React.Component<CharacterPlannerProps, {}> {
  selectClass = (character: Character | undefined,
    characterLearnables: Learnable[],
    requirements: SkillCalculationRequirements) => {
    if (character) {
      const { classId, classes } = requirements;
      const updatedCharacter: Character = {
        ...character,
        selectedClassId: classId
      };
      let updatedEquippedLearnables: Learnable[] = characterLearnables.filter(learnable => {
        return updatedCharacter.equippedLearnableIds.includes(learnable.id);
      });
      const classAbilityIds = classes.byId[classId].classAbilities;
      // if the selected class has class abilities,
      // filter out any previously equipped class abilities
      // before updating the character so the ability is not equipped twice
      if (classAbilityIds.length) {
        const equippedLearnableIds = updatedCharacter.equippedLearnableIds
          .filter(id => !classAbilityIds.includes(id));
        updatedCharacter.equippedLearnableIds = equippedLearnableIds;
        updatedEquippedLearnables = characterLearnables.filter(learnable => {
          return updatedCharacter.equippedLearnableIds.includes(learnable.id);
        });
      }
      updatedCharacter.skillLevels = calculateSkillLevels({
        ...requirements,
        equippedLearnables: updatedEquippedLearnables
      });
      updatedCharacter.requiredClassMasteries = calculateClassMasteriesRequired(updatedEquippedLearnables);
      this.props.updateCharacter(updatedCharacter);
    }
  }

  equipLearnable = (learnableId: string, character: Character | undefined,
    characterLearnables: Learnable[],
    requirements: SkillCalculationRequirements) => {
    if (character) {
      const updatedCharacter: Character = {
        ...character,
        equippedLearnableIds: character.equippedLearnableIds.concat([learnableId])
      };
      const updatedEquippedLearnables = characterLearnables.filter(learnable => {
        return updatedCharacter.equippedLearnableIds.includes(learnable.id);
      });
      updatedCharacter.skillLevels = calculateSkillLevels({
        ...requirements,
        equippedLearnables: updatedEquippedLearnables
      });
      updatedCharacter.requiredClassMasteries = calculateClassMasteriesRequired(updatedEquippedLearnables);
      this.props.updateCharacter(updatedCharacter);
    }
  }

  unequipLearnables = (learnableIds: string[], character: Character | undefined,
    characterLearnables: Learnable[],
    requirements: SkillCalculationRequirements) => {
    if (character) {
      const updatedCharacter: Character = {
        ...character,
        equippedLearnableIds: character.equippedLearnableIds.filter(id => !learnableIds.includes(id))
      };
      const updatedEquippedLearnables = characterLearnables.filter(learnable => {
        return updatedCharacter.equippedLearnableIds.includes(learnable.id);
      });
      updatedCharacter.skillLevels = calculateSkillLevels({
        ...requirements,
        equippedLearnables: updatedEquippedLearnables
      });
      updatedCharacter.requiredClassMasteries = calculateClassMasteriesRequired(updatedEquippedLearnables);
      this.props.updateCharacter(updatedCharacter);
    }
  }

  render() {
    const {
      characters,
      abilities,
      classes,
      selectedCharacterId,
      activeTabIndex,
      selectCharacter,
      setActiveTabIndex,
      globallyLearnables,
      skillLevels,
      spells,
      combatArts
    } = this.props;

    let selectedCharacter: Character | undefined;
    let selectedClass: JobClass | undefined;
    let personalAbility: Ability = {
      id: '',
      description: '',
      notes: ''
    };
    let characterLearnables: Learnable[] = [];
    let equippedLearnables: Learnable[] = [];
    let equippedAbilityIds: string[] = [];
    let availableAbilityLearnables: Learnable[] = [];
    let learnableSpells: Learnable[] = [];
    let requiredClassMasteries: string[] = [];
    let equippedCombatArtIds: string[] = [];
    let availableCombatArtLearnables: Learnable[] = [];

    if (selectedCharacterId) {
      selectedCharacter = characters.byId[selectedCharacterId];
      const equippedIds = selectedCharacter.equippedLearnableIds;
      let classAbilityIds: string[] = [];
      personalAbility = abilities.byId[selectedCharacter.personalAbilities[0]];
      characterLearnables = globallyLearnables.concat(selectedCharacter.learnables);
      if (selectedCharacter.selectedClassId) {
        selectedClass = classes.byId[selectedCharacter.selectedClassId];
        classAbilityIds = selectedClass.classAbilities;
      }

      equippedLearnables = characterLearnables.filter((learnable) => equippedIds.includes(learnable.id));

      equippedAbilityIds = equippedLearnables.filter(learnable => learnable.type === 'Ability')
        .map(learnable => learnable.id);

      equippedCombatArtIds = equippedLearnables
        .filter(learnable => learnable.type === 'Combat Art')
        .map(learnable => learnable.id);

      availableAbilityLearnables = characterLearnables.filter((learnable) => {
        return true && learnable.type === 'Ability' &&
          !equippedIds.includes(learnable.id) &&
          !classAbilityIds.includes(learnable.id);
      }).sort((a, b) => {
        if (a.id < b.id) {
          return -1;
        }
        if (a.id > b.id) {
          return 1;
        }
        return 0;
      });

      availableCombatArtLearnables = characterLearnables.filter((learnable) => {
        return true && learnable.type === 'Combat Art' &&
          !equippedCombatArtIds.includes(learnable.id);
      });

      learnableSpells = characterLearnables.filter((learnable) => {
        return learnable.type === 'Spell';
      });

      requiredClassMasteries = selectedCharacter.requiredClassMasteries;
    }

    return (
      <Container
        fixed
      >
        <Grid
          container
          direction="row"
          spacing={1}
        >
          <Grid
            container
            item
            xs={3}
            spacing={1}
            direction="column"
            style={{
              height: '100vh',
              overflowY: 'auto'
            }}
          >
            <CharacterList
              characters={characters}
              selectedCharacterId={selectedCharacterId}
              selectCharacter={selectCharacter}
            />
          </Grid>
          <Grid
            container
            item
            xs={9}
            spacing={1}
            direction="column"
            style={{
              height: '100vh',
              overflowY: 'auto'
            }}
          >
            {
              selectedCharacter &&
              <React.Fragment>
                <Grid
                  item
                  xs={12}
                >
                  <AppBar position="sticky" color="default">
                    <Tabs
                      variant="fullWidth"
                      value={activeTabIndex}
                      onChange={(e, value) => setActiveTabIndex(value)}
                    >
                      <Tab label="Status" />
                      <Tab label="Skills" />
                      <Tab label="Classes" />
                      <Tab label="Abilities" />
                      <Tab label="Combat Arts" />
                    </Tabs>
                  </AppBar>
                  <TabPanel key="Status" value={activeTabIndex} index={0}>
                    <Typography
                      variant="h2"
                      component="div"
                      style={{
                        padding: '8px'
                      }}
                    >
                      {selectedCharacter.name}
                    </Typography>
                    <Grid
                      container
                      direction="row"
                      alignItems="stretch"
                      justify="space-between"
                    >
                      <Grid
                        item
                        xs={5}
                      >
                        <StatusClassList
                          equippedClass={selectedCharacter.selectedClassId}
                          requiredClasses={requiredClassMasteries}
                        />
                        <CombatArtsList
                          combatArts={combatArts}
                          selectedClass={selectedClass}
                          equippedCombatArtIds={equippedCombatArtIds}
                          character={selectedCharacter}
                          unequipCombatArts={this.unequipLearnables}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                          characterLearnables={characterLearnables}
                          interactive={false}
                        />
                      </Grid>
                      <Grid
                        item
                        xs={5}
                      >
                        <AbilityList
                          selectedClass={selectedClass}
                          personalAbility={personalAbility}
                          abilities={abilities}
                          equippedAbilityIds={equippedAbilityIds}
                          character={selectedCharacter}
                          unequipAbilities={this.unequipLearnables}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                          characterLearnables={characterLearnables}
                          interactive={false}
                        />
                      </Grid>
                    </Grid>
                  </TabPanel>
                  <TabPanel key="Skills" value={activeTabIndex} index={1}>
                    <Grid
                      container
                      direction="row"
                      alignItems="stretch"
                      justify="space-between"
                    >
                      <Grid
                        item
                        xs={5}
                      >
                        <MagicList
                          spells={spells}
                          learnableSpells={learnableSpells}
                        />
                      </Grid>
                      <Grid
                        item
                        xs={5}
                      >
                        <SkillList
                          skills={selectedCharacter.skillLevels}
                        />
                      </Grid>
                    </Grid>
                  </TabPanel>
                  <TabPanel key="Classes" value={activeTabIndex} index={2}>
                    <ClassList
                      classes={classes}
                      characterLearnables={characterLearnables}
                      character={selectedCharacter}
                      selectClass={this.selectClass}
                      skillLevels={skillLevels}
                      equippedLearnables={equippedLearnables}
                    />
                  </TabPanel>
                  <TabPanel key="Abilities" value={activeTabIndex} index={3}>
                    <Grid
                      container
                      direction="row"
                      alignItems="stretch"
                      justify="space-between"
                    >
                      <Grid
                        item
                        xs={4}
                      >
                        <AbilityList
                          selectedClass={selectedClass}
                          personalAbility={personalAbility}
                          abilities={abilities}
                          equippedAbilityIds={equippedAbilityIds}
                          character={selectedCharacter}
                          unequipAbilities={this.unequipLearnables}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                          characterLearnables={characterLearnables}
                          interactive={true}
                        />
                      </Grid>
                      <Grid
                        item
                        xs={4}
                      >
                        <EquippableAbilityList
                          characterLearnables={characterLearnables}
                          availableAbilityLearnables={availableAbilityLearnables}
                          equipAbility={this.equipLearnable}
                          character={selectedCharacter}
                          disabled={equippedAbilityIds.length >= 5}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                        />
                      </Grid>
                    </Grid>
                  </TabPanel>
                  <TabPanel key="Combat Arts" value={activeTabIndex} index={4}>
                    <Grid
                      container
                      direction="row"
                      alignItems="stretch"
                      justify="space-between"
                    >
                      <Grid
                        item
                        xs={4}
                      >
                        <CombatArtsList
                          combatArts={combatArts}
                          selectedClass={selectedClass}
                          equippedCombatArtIds={equippedCombatArtIds}
                          character={selectedCharacter}
                          unequipCombatArts={this.unequipLearnables}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                          characterLearnables={characterLearnables}
                          interactive={true}
                        />
                      </Grid>
                      <Grid
                        item
                        xs={4}
                      >
                        <EquippableCombatArtsList
                          combatArts={combatArts}
                          characterLearnables={characterLearnables}
                          availableCombatArtLearnables={availableCombatArtLearnables}
                          equipCombatArt={this.equipLearnable}
                          character={selectedCharacter}
                          disabled={equippedCombatArtIds.length >= 3}
                          equippedLearnables={equippedLearnables}
                          skillLevels={skillLevels}
                          classId={selectedCharacter.selectedClassId}
                          classes={classes}
                        />
                      </Grid>
                    </Grid>
                  </TabPanel>
                </Grid>
              </React.Fragment>
            }
          </Grid>
        </Grid>

      </Container>
    );
  }
}

export default connect(
  (state: ApplicationState) => state.characterPlannerState,
  (dispatch) => bindActionCreators(CharacterPlannerStore.actionCreators, dispatch)
)(CharacterPlanner);
