import React from 'react';
import { default as CharacterPlanner } from './components/characterPlanner';
import { CssBaseline, MuiThemeProvider, createMuiTheme } from '@material-ui/core';

const theme = createMuiTheme({
  palette: {
    type: 'dark'
  }
});

const App: React.FC = () => {
  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      <CharacterPlanner />
    </MuiThemeProvider>
  );
}

export default App;
