import * as React from 'react';
import {
  Theme,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Divider,
  Tooltip
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import {
  JobClass,
  NormalizedList,
  Character,
  Learnable,
  SkillCalculationRequirements,
  CombatArt
} from '../store';

const useStyles = makeStyles((theme: Theme) => ({
  tooltip: {
    fontSize: '1rem'
  },
  avatar: {
    color: theme.palette.text.primary
  }
}));

interface CombatArtsListProps {
  combatArts: NormalizedList<CombatArt>;
  selectedClass?: JobClass;
  equippedCombatArtIds: string[];
  unequipCombatArts: (combatArtIds: string[], character: Character | undefined, characterLearnables: Learnable[], requirements: SkillCalculationRequirements) => void;
  character: Character;
  characterLearnables: Learnable[];
  skillLevels: string[];
  classId: string;
  classes: NormalizedList<JobClass>;
  equippedLearnables: Learnable[];
  interactive: boolean;
}

const CombatArtsList = (props: React.PropsWithChildren<CombatArtsListProps>) => {
  const {
    combatArts,
    selectedClass,
    equippedCombatArtIds,
    unequipCombatArts,
    character,
    skillLevels,
    classId,
    classes,
    equippedLearnables,
    characterLearnables,
    interactive
  } = props;
  const styleClasses = useStyles();
  return (
    <List>
      <ListItem>
        <ListItemText secondary="Equipped Combat Arts" />
      </ListItem>
      {interactive &&
        equippedCombatArtIds.map((combatArtId) => (
          <React.Fragment key={combatArtId}>
            <Tooltip title={combatArts.byId[combatArtId].description} classes={styleClasses}>
              <ListItem
                button
                onClick={() => unequipCombatArts([combatArtId], character, characterLearnables, {
                  skillLevels,
                  classId,
                  classes,
                  equippedLearnables
                })}
              >
                <ListItemAvatar>
                  <Avatar className={styleClasses.avatar}>
                    {combatArts.byId[combatArtId].weaponType.slice(0, 1)}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary={combatArtId}
                />
              </ListItem>
            </Tooltip>
            <Divider component="li" />
          </React.Fragment>
        ))}
      {!interactive &&
        equippedCombatArtIds.map((combatArtId) => (
          <React.Fragment key={combatArtId}>
            <Tooltip title={combatArts.byId[combatArtId].description} classes={styleClasses}>
              <ListItem>
                <ListItemAvatar>
                  <Avatar className={styleClasses.avatar}>
                    {combatArts.byId[combatArtId].weaponType.slice(0, 1)}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary={combatArtId}
                />
              </ListItem>
            </Tooltip>
            <Divider component="li" />
          </React.Fragment>
        ))}
      {selectedClass && !!selectedClass.combatArt &&
        <React.Fragment>
          <ListItem>
            <ListItemText secondary="Personal Combat Arts" />
          </ListItem>
          <Tooltip
            title={combatArts.byId[selectedClass.combatArt].description}
            classes={styleClasses}
          >
            <ListItem>
              <ListItemAvatar>
                <Avatar className={styleClasses.avatar}>
                  {combatArts.byId[selectedClass.combatArt].weaponType.slice(0, 1)}
                </Avatar>
              </ListItemAvatar>
              <ListItemText
                primary={selectedClass.combatArt}
              />
            </ListItem>
          </Tooltip>
          <Divider component="li" />
        </React.Fragment>
      }
    </List>
  );
};

export default CombatArtsList;
