import {
  NormalizedList,
  JobClass,
  SkillLevel,
  Learnable,
  SkillCalculationRequirements
} from '../store';

export function filterNormalizedList<T>(list: NormalizedList<T>, ids: string[]): NormalizedList<T> {
  let newList: NormalizedList<T> = {
    byId: {},
    allIds: ids
  };

  ids.forEach((id) => {
    newList.byId[id] = list.byId[id];
  });

  return newList;
}

export function calculateSkillLevels(requirements: SkillCalculationRequirements): NormalizedList<SkillLevel> {
  const { skillLevels, classes, classId, equippedLearnables } = requirements;
  // start with a base list of all lowest level skills
  let characterSkillLevels: NormalizedList<SkillLevel> = {
    byId: {
      'Sword': {
        skill: 'Sword',
        level: 'E'
      },
      'Lance': {
        skill: 'Lance',
        level: 'E'
      },
      'Axe': {
        skill: 'Axe',
        level: 'E'
      },
      'Bow': {
        skill: 'Bow',
        level: 'E'
      },
      'Brawl': {
        skill: 'Brawl',
        level: 'E'
      },
      'Reason': {
        skill: 'Reason',
        level: 'E'
      },
      'Faith': {
        skill: 'Faith',
        level: 'E'
      },
      'Authority': {
        skill: 'Authority',
        level: 'E'
      },
      'Hvy Armor': {
        skill: 'Hvy Armor',
        level: 'E'
      },
      'Riding': {
        skill: 'Riding',
        level: 'E'
      },
      'Flying': {
        skill: 'Flying',
        level: 'E'
      }
    },
    allIds: [
      'Sword',
      'Lance',
      'Axe',
      'Bow',
      'Brawl',
      'Reason',
      'Faith',
      'Authority',
      'Hvy Armor',
      'Riding',
      'Flying'
    ]
  };

  // get the class object based on the classId
  const jobClass = classes.byId[classId];
  characterSkillLevels = applySkillLevelsRequiredByClass(jobClass, skillLevels,
    characterSkillLevels);

  equippedLearnables.forEach((learnable) => {
    characterSkillLevels = applySkillLevelsRequiredByLearnable(learnable, skillLevels,
      characterSkillLevels, classes);
  });

  return {
    ...characterSkillLevels
  };
}

function isCurrentSkillLevelSufficient(currentSkillLevel: SkillLevel,
  requiredSkillLevel: SkillLevel, skillLevels: string[]): boolean {
  const requiredLevel = skillLevels.indexOf(requiredSkillLevel.level);
  const currentLevel = skillLevels.indexOf(currentSkillLevel.level);
  return requiredLevel <= currentLevel;
}

function applySkillLevelsRequiredByClass(jobClass: JobClass, skillLevels: string[],
  characterSkillLevels: NormalizedList<SkillLevel>): NormalizedList<SkillLevel> {
  if (jobClass.skillLevels) {
    // if this class has skill level requirements,
    // see if they are already met/exceeded,
    // and if not, set the skill level to the required minimum
    jobClass.skillLevels.forEach((classSkillLevel) => {
      // get the character's skill level currently
      const characterSkillLevel = characterSkillLevels.byId[classSkillLevel.skill];
      // if the skill level is not sufficient, update the skill to the required level
      if (!isCurrentSkillLevelSufficient(characterSkillLevel, classSkillLevel, skillLevels)) {
        characterSkillLevels.byId[classSkillLevel.skill] = {
          ...classSkillLevel
        };
      }
    });
  }

  return {
    ...characterSkillLevels
  };
}

function applySkillLevelsRequiredByLearnable(learnable: Learnable, skillLevels: string[],
  characterSkillLevels: NormalizedList<SkillLevel>,
  classes: NormalizedList<JobClass>): NormalizedList<SkillLevel> {
  if (learnable.learnedFrom === 'Skill' &&
    learnable.skillId &&
    learnable.skillLevel) {
    const currentSkillLevel = characterSkillLevels.byId[learnable.skillId];
    const learnableSkillLevel: SkillLevel = {
      skill: learnable.skillId,
      level: learnable.skillLevel
    };
    if (!isCurrentSkillLevelSufficient(currentSkillLevel, learnableSkillLevel, skillLevels)) {
      characterSkillLevels.byId[learnableSkillLevel.skill] = {
        ...learnableSkillLevel
      };
    }
  } else if (learnable.learnedFrom === 'ClassMastery' &&
    learnable.classMastered) {
    const jobClass = classes.byId[learnable.classMastered];
    characterSkillLevels = applySkillLevelsRequiredByClass(jobClass,
      skillLevels,
      characterSkillLevels);
  }

  return {
    ...characterSkillLevels
  };
}

export function calculateClassMasteriesRequired(equippedLearnables: Learnable[]): string[] {
  let classMasteriesRequired: string[] = [];
  const classMasteryLearnables = equippedLearnables.filter(learnable => learnable.learnedFrom === 'ClassMastery');
  classMasteryLearnables.forEach((learnable) => {
    if (learnable.classMastered && !classMasteriesRequired.includes(learnable.classMastered)) {
      classMasteriesRequired.push(learnable.classMastered);
    }
  });

  return classMasteriesRequired;
}
