import * as React from 'react';
import {
  List,
  ListItem,
  ListItemText,
  Divider,
  Theme
} from '@material-ui/core';
import { Character, NormalizedList } from '../store';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: '100%'
  }
}));

interface CharacterListProps {
  characters: NormalizedList<Character>;
  selectedCharacterId?: string;
  selectCharacter: (id: string) => void;
}

const CharacterList = (props: React.PropsWithRef<CharacterListProps>) => {
  const { selectCharacter, characters, selectedCharacterId } = props;
  const classes = useStyles();
  return (
    <List classes={classes}>
      {characters.allIds.map((characterId) => (
        <React.Fragment key={characterId}>
          <ListItem
            button
            selected={characterId === selectedCharacterId}
            onClick={() => selectCharacter(characterId)}
          >
            <ListItemText primary={characters.byId[characterId].name} />
          </ListItem>
          <Divider />
        </React.Fragment>
      ))}
    </List>
  );
};

export default CharacterList;
