import * as React from 'react';
import {
  Theme,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Divider,
  Tooltip
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import {
  NormalizedList,
  Learnable,
  Character,
  SkillCalculationRequirements,
  JobClass,
  CombatArt
} from '../store';
import { filterNormalizedList } from '../utils';

const useStyles = makeStyles((theme: Theme) => ({
  tooltip: {
    fontSize: '1rem'
  },
  avatar: {
    color: theme.palette.text.primary
  }
}));

interface EquippableCombatArtsListProps {
  characterLearnables: Learnable[];
  availableCombatArtLearnables: Learnable[];
  equipCombatArt: (combatArtId: string, character: Character | undefined, characterLearnables: Learnable[], requirements: SkillCalculationRequirements) => void;
  character: Character;
  disabled?: boolean;
  skillLevels: string[];
  classId: string;
  classes: NormalizedList<JobClass>;
  equippedLearnables: Learnable[];
  combatArts: NormalizedList<CombatArt>;
}

const EquippableCombatArtsList = (props: React.PropsWithChildren<EquippableCombatArtsListProps>) => {
  const {
    availableCombatArtLearnables,
    equipCombatArt,
    character,
    disabled,
    skillLevels,
    classId,
    classes,
    equippedLearnables,
    characterLearnables,
    combatArts
  } = props;
  const styleClasses = useStyles();

  const availableCombatArts = filterNormalizedList(combatArts, availableCombatArtLearnables.map(learnable => learnable.id));

  return (
    <List>
      <ListItem>
        <ListItemText secondary="Available Combat Arts" />
      </ListItem>
      {availableCombatArts.allIds.map((combatArtId) => (
        <React.Fragment key={combatArtId}>
          <Tooltip
            title={combatArts.byId[combatArtId].description}
            classes={styleClasses}
          >
            <span>
              <ListItem
                disabled={disabled}
                button
                onClick={() => equipCombatArt(combatArtId, character, characterLearnables, {
                  skillLevels,
                  classId,
                  classes,
                  equippedLearnables
                })}
              >
                <ListItemAvatar>
                  <Avatar className={styleClasses.avatar}>
                    {combatArts.byId[combatArtId].weaponType.slice(0, 1)}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary={combatArtId}
                />
              </ListItem>
            </span>
          </Tooltip>
          <Divider component="li" />
        </React.Fragment>
      ))}
    </List>
  );
};

export default EquippableCombatArtsList;
