import * as React from 'react';
import {
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  Divider,
  Theme
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { Spell, NormalizedList, Learnable } from '../store';

const useStyles = makeStyles((theme: Theme) => ({
  primary: {
    fontSize: '1.5rem'
  },
  secondary: {
    fontSize: '1.5rem',
    float: 'right',
    color: theme.palette.text.primary
  }
}));

interface MagicListProps {
  spells: NormalizedList<Spell>;
  learnableSpells: Learnable[];
}

const MagicList = (props: React.PropsWithRef<MagicListProps>) => {
  const {
    spells,
    learnableSpells
  } = props;
  learnableSpells.sort((a, b) => {
    // reorder so that Reason skills come first, then Faith
    if (a.skillId === 'Reason' && b.skillId === 'Faith') {
      return -1;
    }
    if (a.skillId === 'Faith' && b.skillId === 'Reason') {
      return 1;
    }
    return 0;
  });
  const classes = useStyles();
  return (
    <List
      subheader={
        <ListSubheader>
          Learnable Magic
        </ListSubheader>
      }
    >
      {learnableSpells.map((spell) => (
        <React.Fragment key={spell.id}>
          <ListItem>
            <ListItemText
              classes={classes}
              primary={spell.id}
              secondary={spell.skillLevel}
              primaryTypographyProps={{
                variant: "body1",
                component: "span"
              }}
              secondaryTypographyProps={{
                variant: "body1",
                component: "span"
              }}
            />
          </ListItem>
          <Divider />
        </React.Fragment>
      ))}
    </List>
  );
};

export default MagicList;
