import { Reducer } from 'redux';
import {
  CharacterPlannerState,
  AppThunkAction,
  actionSelectCharacter,
  actionSetActiveTabIndex,
  actionUpdateCharacter,
  SelectCharacterAction,
  SetActiveTabIndexAction,
  UpdateCharacterAction
} from '.';
import {
  selectCharacter,
  setActiveTabIndex,
  updateCharacter
} from '../actions';
import { initialState } from './initialState'

type KnownAction = 
  SelectCharacterAction |
  SetActiveTabIndexAction |
  UpdateCharacterAction;

export const actionCreators = {
  selectCharacter: (id: string): AppThunkAction<KnownAction> => (dispatch, getState) => {
    // set the active tab back to the first tab
    dispatch(setActiveTabIndex(0));
    // then select the character
    dispatch(selectCharacter(id));
  },
  setActiveTabIndex,
  updateCharacter
};

export const reducer: Reducer<CharacterPlannerState> = (state = initialState, incomingAction) => {
  const action = incomingAction as KnownAction;

  switch (action.type) {
    case actionSelectCharacter:
      return {
        ...state,
        selectedCharacterId: (action as SelectCharacterAction).id
      };
    case actionSetActiveTabIndex:
      return {
        ...state,
        activeTabIndex: (action as SetActiveTabIndexAction).index
      };
    case actionUpdateCharacter:
      return {
        ...state,
        characters: {
          byId: {
            ...state.characters.byId,
            [(action as UpdateCharacterAction).character.id]: (action as UpdateCharacterAction).character
          },
          allIds: [...state.characters.allIds]
        }
      };
    default:
      return state;
  }
};
