import * as React from 'react';
import {
  List,
  ListItem,
  ListItemText,
  Divider
} from '@material-ui/core';

interface StatusClassListProps {
  requiredClasses: string[];
  equippedClass: string;
}

const StatusClassList = (props: React.PropsWithChildren<StatusClassListProps>) => {
  const {
    requiredClasses,
    equippedClass
  } = props;
  return (
    <List>
      <ListItem>
        <ListItemText secondary="Equipped Class" />
      </ListItem>
      <ListItem>
        <ListItemText
          primary={equippedClass}
        />
      </ListItem>
      <Divider component="li" />
      <ListItem>
        <ListItemText secondary="Required Class Masteries" />
      </ListItem>
      {requiredClasses.map((jobClass) => (
        <React.Fragment key={jobClass}>
          <ListItem>
            <ListItemText
              primary={jobClass}
            />
          </ListItem>
          <Divider component="li" />
        </React.Fragment>
      ))}
    </List>
  );
};

export default StatusClassList;
