// define actions as string constants
export const actionSelectCharacter = 'SELECT_CHARACTER';
export const actionSetActiveTabIndex = 'SET_ACTIVE_TAB_INDEX';
export const actionUpdateCharacter = 'UPDATE_CHARACTER';

// define an object with the action constants as their keys
const allActions = {
  [actionSelectCharacter]: "",
  [actionSetActiveTabIndex]: "",
  [actionUpdateCharacter]: ""
};

// use the allActions object to restrict type to only strings
// that are defined as constants
export interface AppAction {
  type: keyof typeof allActions;
}

export interface SelectCharacterAction extends AppAction {
  id: string;
}

export interface SetActiveTabIndexAction extends AppAction {
  index: number;
}

export interface UpdateCharacterAction extends AppAction {
  character: Character;
}

export interface NormalizedList<T> {
  byId: { [id: string]: T };
  allIds: string[];
}

export interface AppThunkAction<TAction> {
  (dispatch: (action: TAction) => void, getState: () => ApplicationState): void;
}

export interface SkillCalculationRequirements {
  skillLevels: string[];
  classId: string;
  classes: NormalizedList<JobClass>;
  equippedLearnables: Learnable[];
}

export interface Growth {
  hp?: number;
  str?: number;
  mag?: number;
  dex?: number;
  spd?: number;
  lck?: number;
  def?: number;
  res?: number;
  cha?: number;
}

export interface Learnable {
  id: string;
  description?: string;
  type: 'Ability' | 'Combat Art' | 'Spell';
  learnedFrom: 'Skill' | 'ClassMastery';
  skillId?: string;
  skillLevel?: string;
  classMastered?: string;
  classEquipped?: string;
}

export interface SkillLevel {
  skill: string;
  level: string;
}

export interface Spell {
  id: string;
  description: string;
  mt: string;
  hit: string;
  crit: string;
  rng: string;
  minRng?: number;
  maxRng?: number;
  maxRngNumerator?: string;
  maxRngDenominator?: number;
  wt: string;
  use: string;
}

export interface Ability {
  id: string;
  description: string;
  notes: string;
}

export interface CombatArt {
  id: string;
  description: string;
  weaponType: 'Sword' | 'Lance' | 'Axe' | 'Bow' | 'Gauntlet' | 'Other';
  might: string;
  hit: string;
  crit: string;
  rng: string;
  durabilityCost: string;
}

export interface JobClass {
  id: string;
  description?: string;
  classAbilities: string[];
  masterAbilities: string[];
  skillLevels?: SkillLevel[];
  combatArt?: string;
  growth?: Growth;
}

export interface Character {
  id: string;
  name: string;
  selectedClassId: string;
  personalAbilities: string[];
  availableClasses: string[];
  equippedLearnableIds: string[];
  learnables: Learnable[];
  skillLevels: NormalizedList<SkillLevel>;
  requiredClassMasteries: string[];
  baseGrowth?: Growth;
  classGrowth?: Growth;
  hp?: number;
  str?: number;
  mag?: number;
  dex?: number;
  spd?: number;
  lck?: number;
  def?: number;
  res?: number;
  cha?: number;
}

export interface CharacterPlannerState {
  characters: NormalizedList<Character>;
  abilities: NormalizedList<Ability>;
  classes: NormalizedList<JobClass>;
  spells: NormalizedList<Spell>;
  combatArts: NormalizedList<CombatArt>;
  skills: string[];
  skillLevels: string[];
  globallyLearnables: Learnable[];
  selectedCharacterId?: string;
  activeTabIndex: number;
}

export interface ApplicationState {
  characterPlannerState: CharacterPlannerState;
}
