import * as React from 'react';
import {
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  Divider,
  Theme
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { SkillLevel, NormalizedList } from '../store';

const useStyles = makeStyles((theme: Theme) => ({
  primary: {
    fontSize: '1.5rem'
  },
  secondary: {
    fontSize: '1.5rem',
    float: 'right',
    color: theme.palette.text.primary
  }
}));

interface SkillListProps {
  skills: NormalizedList<SkillLevel>;
}

const SkillList = (props: React.PropsWithRef<SkillListProps>) => {
  const { skills } = props;
  const classes = useStyles();
  return (
    <List
      subheader={
        <ListSubheader>
          Skill Levels
        </ListSubheader>
      }
    >
      {skills.allIds.map((skillId) => (
        <React.Fragment key={skillId}>
          <ListItem>
            <ListItemText
              classes={classes}
              primary={skillId}
              secondary={skills.byId[skillId].level}
              primaryTypographyProps={{
                variant: "body1",
                component: "span"
              }}
              secondaryTypographyProps={{
                variant: "body1",
                component: "span"
              }}
            />
          </ListItem>
          <Divider />
        </React.Fragment>
      ))}
    </List>
  );
};

export default SkillList;
