import * as React from 'react';
import {
  Theme,
  List,
  ListItem,
  ListItemText,
  Divider,
  Tooltip
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import {
  JobClass,
  Ability,
  NormalizedList,
  Character,
  Learnable,
  SkillCalculationRequirements
} from '../store';

const useStyles = makeStyles((theme: Theme) => ({
  tooltip: {
    fontSize: '1rem'
  }
}));

interface AbilityListProps {
  personalAbility: Ability;
  selectedClass?: JobClass;
  abilities: NormalizedList<Ability>;
  equippedAbilityIds: string[];
  unequipAbilities: (abilityIds: string[], character: Character | undefined, characterLearnables: Learnable[], requirements: SkillCalculationRequirements) => void;
  character: Character;
  characterLearnables: Learnable[];
  skillLevels: string[];
  classId: string;
  classes: NormalizedList<JobClass>;
  equippedLearnables: Learnable[];
  interactive: boolean;
}

const AbilityList = (props: React.PropsWithChildren<AbilityListProps>) => {
  const {
    personalAbility,
    selectedClass,
    abilities,
    equippedAbilityIds,
    unequipAbilities,
    character,
    skillLevels,
    classId,
    classes,
    equippedLearnables,
    characterLearnables,
    interactive
  } = props;
  const styleClasses = useStyles();
  return (
    <List>
      <ListItem>
        <ListItemText secondary="Personal" />
      </ListItem>
      <Tooltip title={personalAbility.description} classes={styleClasses}>
        <ListItem>
          <ListItemText
            primary={personalAbility.id}
          />
        </ListItem>
      </Tooltip>
      <Divider component="li" />
      {
        selectedClass &&
        <React.Fragment>
          <ListItem>
            <ListItemText secondary="Class" />
          </ListItem>
          {!!selectedClass.classAbilities.length &&
            selectedClass.classAbilities.map((abilityId) => (
              <React.Fragment key={abilityId}>
                <Tooltip title={abilities.byId[abilityId].description} classes={styleClasses}>
                  <ListItem>
                    <ListItemText
                      primary={abilityId}
                    />
                  </ListItem>
                </Tooltip>
                <Divider component="li" />
              </React.Fragment>
            ))}
          {!selectedClass.classAbilities.length &&
            <React.Fragment>
              <ListItem>
                <ListItemText
                  primary="None"
                />
              </ListItem>
              <Divider component="li" />
            </React.Fragment>
          }
        </React.Fragment>
      }
      <ListItem>
        <ListItemText secondary="Equipped" />
      </ListItem>
      {interactive &&
        equippedAbilityIds.map((abilityId) => (
          <React.Fragment key={abilityId}>
            <Tooltip title={abilities.byId[abilityId].description} classes={styleClasses}>
              <ListItem
                button
                onClick={() => unequipAbilities([abilityId], character, characterLearnables, {
                  skillLevels,
                  classId,
                  classes,
                  equippedLearnables
                })}
              >
                <ListItemText
                  primary={abilityId}
                />
              </ListItem>
            </Tooltip>
            <Divider component="li" />
          </React.Fragment>
        ))}
      {!interactive &&
        equippedAbilityIds.map((abilityId) => (
          <React.Fragment key={abilityId}>
            <Tooltip title={abilities.byId[abilityId].description} classes={styleClasses}>
              <ListItem>
                <ListItemText
                  primary={abilityId}
                />
              </ListItem>
            </Tooltip>
            <Divider component="li" />
          </React.Fragment>
        ))}
    </List>
  );
};

export default AbilityList;
